.pizza_input {
    height: 12%;
    display: flex;
    flex-direction: column;
    align-items: stretch;

    @media(orientation: portrait) {
        height: 12%; }

    &--unos {
        margin-top: 21%; }

    &--dos {
        margin-bottom: 21%; }

    &__title {
        color: #3dbacc;
        font-size: 5vh;
        margin: 5px 0;
        @media(orientation: portrait) {
            font-size: 5vw;
            margin: 2px 0; } }

    &__form {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 10px 9; }

    &__label {
        display: block;
        font-size: 3vh;
        @media(orientation: portrait) {
            font-size: 3vw; } }

    &__input {
        text-align: center;
        flex-grow: 1;
        width: 20%;
        margin: 5px 0;
        padding: 4px;
        transition: .3s;
        font-size: 2vh;
        color: #292929;
        border: 2px solid #e3e0e0;
        border-radius: 10px;
        outline: none;

        @media(orientation: portrait) {
            border-radius: 4px;
            font-size: 2vw;
            padding: 0;
            margin: 2px 0;
            border-width: 1px; }

        &:focus {
            border-color: #3dbacc;
            border-radius: 14px;
            @media(orientation: portrait) {
                border-radius: 8px; } }

        &--valid {
            border-color: #53da8b; }

        &--invalid {
            border-color: #E7655C; } } }
